import './App.css';
import ImgMarkup from 'react-img-markup'
import { triggerBase64Download } from 'react-base64-downloader';

function App() {

  const defaultValues = {
    color: '#0af',
    fontSize: 40,
    strokeWidth: 5,
    type:"text",
    text: ""
  }

  return (
    <div className = "flex flex-wrap justify-center">
      <div className='title text-2xl font-semibold font-sans text-amber-600'>
        <p>点击图片可添加文字</p>
      </div>
      <ImgMarkup 
        imgSrc='./will_smith.jpg' 
        defaultValues={defaultValues}
        onSave={(uri) => {
          triggerBase64Download(uri, "slap")
        }}>
        {({
          activeColor,
          activeType,
          activeFontSize,
          setActiveColor,
          setActiveType,
          setActiveFontSize,
          save, 
          undo}) => (
          <>

            <div className='selector justify-center'>
            <label htmlFor="type">类型: </label>
              <select name='type' value={activeType} onChange={(e) => setActiveType(e.target.value)}>
                <option value='line'>线</option>
                <option value='arrow'>箭头</option>
                <option value='rect'>矩形</option>
                <option value='ellipse'>圆形</option>
                <option value='text'>文字</option>
              </select>

              <label htmlFor="color" className='label-sp'>颜色: </label>
              <select name='color' value={activeColor} onChange={(e) => setActiveColor(e.target.value)}>
                <option value='red'>红色</option>
                <option value='green'>绿色</option>
                <option value='#0af'>蓝色</option>
                <option value='orange'>黄色e</option>
              </select>

              <label htmlFor='font-size' className='label-sp'>字体大小: </label>
              <input name='font-size' type='number' value={activeFontSize} onChange={e => setActiveFontSize(e.target.value)} />
            </div>
            <div className='btns'>
              <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded" onClick={save}>保存</button>
              <button className='bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded btn-sp' onClick={undo}>撤销</button>
            </div>
          </>
        )}
      </ImgMarkup>
    </div>
  );
}

export default App;
